<template>
  <div class="app">
    <div class="nav-button" :class="openClass" @click="toggleNav()">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>

    <div class="nav" :class="openClass">
      <div class="nav-content">
        <div class="nav-header">
          <h3>fai-dnd-vue</h3>
          <div class="divider"></div>
        </div>
        <div>
          <div
            v-for="section in pages"
            :key="section.title"
            class="menu-section"
          >
            <h4>{{ section.title }}</h4>
            <ul>
              <router-link
                v-for="page in section.pages"
                :key="page.title"
                tag="li"
                :to="page.name"
                >{{ page.title }}
              </router-link>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <div class="content">
      <div class="header" :class="openClass">
        {{ $router.currentRoute.meta.title }}
        <div class="source-code" @click="openCode()">
          <img
            src="data:image/svg+xml;base64,"
            alt=""
          />
          <span>source</span>
        </div>
      </div>
      <div class="demo">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import pages from "./config/navigation";

export default {
  name: "Demo",
  data: function() {
    return {
      pages,
      isNavOpen: true
    };
  },
  methods: {
    toggleNav: function() {
      this.isNavOpen = !this.isNavOpen;
    },
    openCode() {
      const name = this.$router.currentRoute.name;
      const url = `http://gitlab.faidev.cc/frontend/fai-dnd-vue/tree/master/demo/src/pages/${name}.vue`;
      window.open(url, "_blank");
    }
  },

  computed: {
    openClass() {
      return this.isNavOpen ? "open" : "closed";
    }
  }
};
</script>



